.form {
  margin-top: 220px;
  margin-left: 80px;
  display: flex;
}
.input {
  width: 120px;
  border: none;
  background-color: rgba(0, 0, 0, 0);
  margin-left: 30px;
}
.button_container {
  width: 70px;
  height: 20px;
  background-image: url('../img/lastik.png');
  background-repeat: no-repeat;
  background-size: contain;
}
.button {
  border: none;
  background-color: rgba(0, 0, 0, 0);
  margin-left: 7px;
  font-family: 'Dancing Script';
  cursor: pointer;
}
.button_container:hover {
  transform: scale(1.2);
}
.input_container {
  width: 170px;
  background-image: url('../img//pensil.png');
  background-repeat: no-repeat;
  background-size: contain;
}
